@import url(<link href="https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap" rel="stylesheet">);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App {
  font-family: 'Balsamiq Sans', cursive;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.display-flex{
  display: flex;
}

.flex-col{
  flex-direction: column;
}

.node {
  width: 25px;
  height: 25px;
  outline: 1px solid rgb(175, 216, 248);
}

.finish {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.start {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.visited {
  -webkit-animation-name: visitedAnimation;
          animation-name: visitedAnimation;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgba(255, 153, 51, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(179, 0, 179, 0.75);
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgba(153, 0, 204 0.75);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(102, 0, 204, 0.75)
  }
}

@keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgba(255, 153, 51, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(179, 0, 179, 0.75);
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgba(153, 0, 204 0.75);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(102, 0, 204, 0.75)
  }
}

.wall {
  background-color: rgb(40, 40, 80);
}

.shortest-path {
  -webkit-animation-name: shortestPath;
          animation-name: shortestPath;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes shortestPath {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    background-color: rgba(0, 217, 159, 0.75);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 217, 159, 0.75);
  }
}

@keyframes shortestPath {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    background-color: rgba(0, 217, 159, 0.75);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 217, 159, 0.75);
  }
}

.shortest-path-start{
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  opacity: 0.4;
}

.shortest-path-finish{
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}


.grid {
  margin: 16px 8px 0px 8px;
}

#controls{
  border-top: 32px solid rgba(179, 0, 179, 0.75);
  padding: 8px;
}

#primary-button{
  border-radius: 8px;
  background-color: rgba(0, 217, 159, 0.75);
  border: none;
  padding: 12px 8px;
  margin-right: 8px;
  color: #ffffff;
  font-family: 'Balsamiq Sans', cursive;
  font-weight: 600;
}

#secondary-button{
  border-radius: 8px;
  color: rgba(0, 217, 159, 0.75);
  border: 1px solid rgba(0, 217, 159, 0.75);
  padding: 12px 8px;
  margin-right: 8px;
  background-color: #ffffff;
  font-family: 'Balsamiq Sans', cursive;
  font-weight: 600;
}

.legends{
  padding: 8px;
}

.legend{
  width: 25px;
  height: 25px;
}
